@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
html, body, .App, #root{
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Source Sans Pro', 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  text-align: center;
}

h1 {
  font-size: 2.7rem !important;
}

h2 {
  font-size: 2.2rem !important;
}

h3 {
  font-size: 1.5rem !important;
}

h4 {
  font-size: 1.2rem !important;
}

h4 {
  font-size: 1.1rem !important;
}
.particles-bg {
    position: absolute;
    width: 100%;
    height: 100%;
}

.introduction {
    height: 100vh;
    box-sizing: border-box;
    background-image: linear-gradient(180deg, #088dff 20%, #FFF 100%);
}

.introduction-content {
    position: relative;
    z-index: 2;
}

.profile-image {
    border-radius: 50% !important;
    width: 200px;
    margin-top: 100px;
    border: solid lightgrey 5px;
    box-shadow: 0 20px 80px rgba(0,0,0, 0.2);
}

.name {
    margin-top: 20px;
}

.text-box {
    width: 50%;
    margin: 100px auto auto;
}

.social {
    margin-top: 150px;
    font-size: 1.05rem;
}

.social > .contact-icon {
    color: black;
}

.contact-text {
    margin-left: 10px;
    vertical-align: middle;
}

social > .linkedin-icon {
    text-align: right;
}


social > .github-icon {
    text-align: left;
}

h1 {
    /*color: #84b1f3;*/
}
#experience {
    padding: 40px 0;
}

.timeline-page {
    padding: 10px 0;
    position: relative;
    margin-top: 30px;
}

.timeline-page::after {
    content: "";
    height: 100%;
    left: 0;
    margin: auto;
    position: absolute;
    right: 0;
    top: 0;
    width: 1px;
}

.under-line {
    display: inline-block;
    position: relative;
    width: 170px;
    height: 1px;
    margin: 5px auto 10px;
}

.line-up {
    position: absolute;
    line-height: 1;
    left: 1px;
    width: 100%;
    top: -9px;
}

.dot {
    display: inline-block;
    width: 8px;
    height: 8px;
    transform: rotate(-45deg);
    margin-right: 2px;
}

.under-line,
.dot,
.container-filter li a.active,
.timeline-page::after,
.timeline-page .timeline-item .company:after,
.timeline-page .timeline-item .location:after,
.timeline-page .timeline-item .date:after,
.timeline-page .timeline-item .tools:after{
    background-color: #0089ff;
}


.tool-pill {
    display: inline;
    border: solid 1px #0083ff;
    border-radius: 8px;
    padding: 4px 6px;
    font-size: 0.8rem;
    margin: 0 5px;
}

.tool-color-language {
    border-color: #941313;
}

.tool-color-web {
    border-color: #ce05ef;
}

.tool-color-library {
    border-color: #4d11ca;
}

.tool-color-tool {
    border-color: #275a30;
}

.tool-color-hardware {
    border-color: #14caba;
}

.tool-color-cloud {
    border-color: #fca403;
}

.tool-color-devops {
    border-color: #000000;
}
/*TODO: Move left right class onto timeline-time instead of each child*/

.timeline-item {
    margin-bottom: 30px;
}

.timeline-page .timeline-item .company:after,
.timeline-page .timeline-item .location:after,
.timeline-page .timeline-item .date:after,
.timeline-page .timeline-item .tools:after {
    border: 3px solid rgba(255, 255, 255, 0.8);
    border-radius: 50%;
    content: "";
    height: 20px;
    position: absolute;
    top: 25px;
    width: 20px;
    z-index: 1;
}
.timeline-page .timeline-item .left .company:after,
.timeline-page .timeline-item .left .location:after,
.timeline-page .timeline-item .left .date:after,
.timeline-page .timeline-item .left .tools:after {
    right: -10px;
}

.timeline-page .timeline-item .right .company:after,
.timeline-page .timeline-item .right .location:after,
.timeline-page .timeline-item .right .date:after,
.timeline-page .timeline-item .right .tools:after {
    left: -10px;
}

.timeline-page .timeline-item .item-box {
    padding: 20px 20px 20px 20px;

}

.timeline-page .timeline-item .item-description.left {
    text-align: left;
    margin-left: 15px;
}

.timeline-page .timeline-item .item-description {
    text-align: left;
    margin-right: 15px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
}

.timeline-subtitle {
    line-height: 26px;
    font-size: 1.05rem;
    color: #666;
}

.timeline-item .item-info {
    font-size: 1.05rem;
}

.timeline-item .item-info.left{
    text-align: right;
}

.timeline-item .item-info.right{
    text-align: left;
}

.company,
.date,
.location,
.tools,
.project-link,
.github {
    margin-bottom: 2px;
}

.company-icon.left,
.date-icon.left,
.location-icon.left,
.link-icon.left,
.github-icon.left {
    margin-left: 10px;
}

.company-icon.right,
.date-icon.right,
.location-icon.right,
.link-icon.right,
.github-icon.right {
    margin-right: 10px;
    float: left;
}

.tools-icon.left{
    margin-left: 5px;
}

.tools-icon.right{
    margin-right: 5px;
    float: left;
}

.project-link > a,
.github > a {
    vertical-align: middle;
}

.timeline-item .item-description-list {
    list-style-type: none;
    margin: 0;
    padding: 0 0 0 1.5em;
    text-indent: -1.5em;
}

.item-description-list > li:before {
    content: "\2BC8";
    padding-right: 0.5em;
    color: black;
}

.item-description .arrow {
    position: absolute;
    width: 20px;
    height: 20px;
    top: 25px;
    transform: rotate(45deg);
    background-color: white;
    z-index: 0;
}

.item-description .arrow.left {
    left: 20px;
    box-shadow: -1px 1px 2px 0px rgba(0,0,0,0.1);
}

.item-description .arrow.right {
    right: 20px;
    box-shadow: 3px -3px 5px rgba(0, 0, 0, 0.1);
}
#projects {
    padding: 40px 0;
    margin-bottom: 50px;
}
